.App {
  text-align: center;
  display: grid;
}

.App-header {
  background-color: #282c34;
  height: 8vh;
  min-height: 70px;
  width: 100vw;
  font-size: 1rem;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  
  color: white;
  padding: 20px;
}

.App-header>div {
  text-align: start;
  margin-right: 30px;
}

.Grid {
  display: grid;
  grid-template-columns: repeat(3, auto);
  justify-content: center;
  row-gap: 10px;
  column-gap: 30px;
  margin-top: 50px;
}

img {
  height: 200px;
}

/* Hover effect from Alvaro Trigo - https://alvarotrigo.com/blog/best-css-button-hover-effects/ */
.Card {
  width: 220px;
  border: none;
  outline: none;
  color: #fff;
  background-color: #282c34;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  padding: 15px;
}

.Card:before {
  content: '';
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

.Card:active {
  color: #000
}

.Card:active:after {
  background: transparent;
}

.Card:hover:before {
  opacity: 1;
}

.Card:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #282c34;
  left: 0;
  top: 0;
  border-radius: 10px;
}
